@import '../../assets/variables.scss';

.container {
    display: flex;
    flex-direction: column;
    align-items: center;

    &__PillowsBlankets {
        min-height: calc(100vh - 50px);
    }

    & h2 {
        margin: 70px 2em 0 2em;
    }

    & p {
        margin: 1em 2em;
    }

    &::after {
        content: "";
        background-image: $bg-image;
        background-size: cover;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        opacity: 0.8;
        z-index: -1;
    }

    &__button {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        // margin: auto;
        justify-content: center;

        button {
            margin: 10px 10px 20px 10px;
            height: 150px;
            width: 300px;
            border-radius: 10px;
            background-color: rgb(251, 234, 237);
            cursor: pointer;
            font-size: calc((100vw + 3590px) / 215);

            &:hover {
                height: 155px;
                width: 305px;
                background-color: rgb(229, 186, 193);
                font-size: calc((100vw + 3590px) / 200)
            }
        }
    }
}
